html,body{
  background-color:$secondColor;
}
main{
  background-color: $firstColor;
}
 
header img{
  width: 65%;
}

div#root,.panel{
  display: block;
}


@media screen and (orientation:landscape) 
{
  div#root,.panel{
    display: block;
    height: 100%;
  }
  main{
    height: 100%;
  }
}
 
@media screen and (orientation:portrait) 
{
  header{
    background-color:$secondColor;
  }
  html,body{
    background-color:$firstColor;
  } 
}